.wrapper {
  display: flex;  
  flex-flow: row wrap;
  font-weight: bold;
  text-align: center;
}

.wrapper > * {
  padding: 1em;
  flex: 1 100%;
}

.footer {
  border-top: 1px solid gray;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* border-left: 1px solid gray;
  border-right: 1px solid gray; */
}

.aside-1 {
  display: none;
}

.aside-2 {
  display: none;
}

@media all and (min-width: 600px) {
  .aside { flex: 1 0 0; }
}

@media all and (min-width: 800px) {
  .main    { flex: 3 0px;}
  .aside-1 { order: 1; display: unset} 
  .main    { order: 2; }
  .aside-2 { order: 3; display: unset}
  .footer  { order: 4; }
}

body {
  /*padding: 2em; */
  background-color: black;
}

.navigation {
  background-color:rgb(27, 27, 27);
  display: flex;
  flex-flow: row wrap;
  /* This aligns items to the end line on main-axis */
  justify-content: flex-start;
  align-items: center;
}

/* Medium screens */
@media all and (max-width: 800px) {
  .navigation {
    /* When on medium sized screens, we center it by evenly distributing empty space around items */
    justify-content: space-around;
  }
}

/* Small screens */
@media all and (max-width: 500px) {
  .navigation {
    /* On small screens, we are no longer using row direction but column */
    flex-direction: column;
  }
}

.navigation-item {
  color: rgb(47,215,252);
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  padding-left: 1em;
  padding-right: 1em;
  font-size: small;
}

.app-logo {
  height: 0.9em;
  width: auto;
}

.main-image {
  padding-top: 2em;
  width: 20em;
  height: auto;
}